/* eslint-disable max-classes-per-file */
import { MemberGroupPreview } from '@/apis/models/MemberGroupModel';
import { QuizGroupPreview } from '@/apis/models/QuizGroupModel';

export interface StateRecord {
  state: string;
  datetime: string;
}

export interface QuizEventStateModel {
  isLocked: boolean;

  stateHistory: StateRecord[] | null; // under development
}

export interface QuizEventModel {
  uuid: string;
  name: string;
  startTime: string;
  endTime: string;
  scheduledTime: string | null;
  shortId: string;
  quizGroups: QuizGroupPreview[] | null;
  memberGroups: MemberGroupPreview[] | null;
  isLocked: boolean;

  stateHistory: StateRecord[] | null; // under development
}

export interface QuizEventPreview {
  uuid: string;
  name: string;
  startTime: string;
  endTime: string;
  scheduledTime: string;
  shortId: string;
  quizGroupCount: number;
  memberGroupCount: number;
}

export interface UpdateQuizEventPayloadModel {
  name?: string;
  startTime?: string;
  endTime?: string;
  scheduledTime?: string;
  quizGroupIdList?: string[];
  memberGroupIdList?: string[];
}

export enum QuizEventAction {
  SEND = 'send'
}

export class QuizEvent implements QuizEventModel {
  uuid: string;

  name: string;

  startTime: string;

  endTime: string;

  scheduledTime: string | null;

  shortId: string;

  quizGroups: QuizGroupPreview[] | null;

  memberGroups: MemberGroupPreview[] | null;

  isLocked: boolean;

  stateHistory: StateRecord[] | null;

  constructor(name = '', startTime = '', endTime = '') {
    this.uuid = '';
    this.name = name;
    this.startTime = startTime;
    this.endTime = endTime;
    this.scheduledTime = null;
    this.shortId = '';
    this.quizGroups = null;
    this.memberGroups = null;
    this.isLocked = false;
    this.stateHistory = null;
  }
}

export class UpdateQuizEventPayload implements UpdateQuizEventPayloadModel {
  name?: string;

  startTime?: string;

  endTime?: string;

  scheduledTime?: string;

  quizGroupIdList?: string[];

  memberGroupIdList?: string[];

  constructor(name: string | null,
    startTime: string | null,
    endTime: string | null,
    scheduledTime: string | null,
    quizGroups: QuizGroupPreview[] | null,
    memberGroups: MemberGroupPreview[] | null) {
    if (name) {
      this.name = name;
    }

    if (startTime) {
      this.startTime = startTime;
    }

    if (endTime) {
      this.endTime = endTime;
    }

    if (scheduledTime) {
      this.scheduledTime = scheduledTime;
    }

    if (quizGroups) {
      this.quizGroupIdList = [];
      for (let i = 0; i < quizGroups.length; i += 1) {
        this.quizGroupIdList.push(quizGroups[i].uuid);
      }
    }

    if (memberGroups) {
      this.memberGroupIdList = [];
      for (let i = 0; i < memberGroups.length; i += 1) {
        this.memberGroupIdList.push(memberGroups[i].uuid);
      }
    }
  }
}
