import api from '@/apis/BaseApi';
import {
  QuizEvent, QuizEventAction, QuizEventPreview, QuizEventStateModel, UpdateQuizEventPayload, UpdateQuizEventPayloadModel,
} from '@/apis/models/QuizEventModel';

export default class QuizEventApi {
  /**
   * Create a new QuizEvent for a workspace
   *
   * @param workspaceId Workspace to create a new QuizEvent
   */
  static async create(workspaceId: string): Promise<QuizEvent> {
    const result = await api.post(`workspace/${workspaceId}/quiz-events/`, {});
    return result.data.quizEvent;
  }

  /**
   * Get a specific QuizEvent in a workspace
   *
   * @param workspaceId
   * @param quizEventId
   */
  static async getQuizEvent(workspaceId: string, quizEventId: string): Promise<QuizEvent> {
    const result = await api.get(`workspace/${workspaceId}/quiz-events/${quizEventId}`);
    return result.data.quizEvent;
  }

  /**
   * Get all QuizEvnet within a workspace
   *
   * @param workspaceId
   */
  static async getQuizEvents(workspaceId: string): Promise<QuizEventPreview[]> {
    const result = await api.get(`workspace/${workspaceId}/quiz-events/`);
    return result.data.quizEventList;
  }

  /**
   * Get current state of a QuizEvent
   *
   * @param workspaceId
   * @param quizEventId
   */
  static async getQuizEventState(workspaceId: string, quizEventId: string): Promise<QuizEventStateModel> {
    const result = await api.get(`workspace/${workspaceId}/quiz-events/${quizEventId}/state`);
    return result.data.quizEventState;
  }

  /**
   * Update the data of a QuizEvent by a quizEvent object
   * if some values in quizEvent are not set (null or undefined), these null values will not be updated (remain the same in backend database)
   *
   * When the QuizEvent is locked, only startTime, endTime and name could be updated.
   * If you try to update other properties when QuizEvent is locked, you will receive "409 Conflict" error.
   *
   * @param workspaceId
   * @param quizEventId
   * @param quizEvent the data used to update QuizEvent specified by quizEventId
   */
  static async update(workspaceId: string, quizEventId: string, quizEvent: QuizEvent): Promise<void> {
    const {
      name, startTime, endTime, scheduledTime, quizGroups, memberGroups,
    } = quizEvent;
    const updatePayload = new UpdateQuizEventPayload(name, startTime, endTime, scheduledTime, quizGroups, memberGroups);

    await this.put(workspaceId, quizEventId, updatePayload);
  }

  private static async put(workspaceId: string, quizEventId: string, updatePayload: UpdateQuizEventPayloadModel): Promise<void> {
    const body = {
      ...updatePayload,
    };
    await api.put(`workspace/${workspaceId}/quiz-events/${quizEventId}`, body);
  }

  /**
   * Notify the backend server to do specific action on QuizEvent
   *
   * @param workspaceId
   * @param quizEventId
   * @param action specify the action you want to perform
   */
  static async doAction(workspaceId: string, quizEventId: string, action: QuizEventAction): Promise<void> {
    const body = {
      action,
    };

    await api.post(`workspace/${workspaceId}/quiz-events/${quizEventId}/action`, JSON.stringify(body));
  }

  /**
   * Delete a specific QuizEvent in a workspace
   * **The Deleting operation will permenently delete the content**
   *
   * @param workspaceId
   * @param quizEventId
   */
  static async delete(workspaceId: string, quizEventId: string): Promise<void> {
    await api.delete(`workspace/${workspaceId}/quiz-events/${quizEventId}`);
  }
}
