
















import Vue from 'vue';
import QuizEventCard from '@/components/workspace/QuizEventCard.vue';
import ManagerApi from '@/apis/ManagerApi';
import { ManagerEntryInfoModel } from '@/apis/models/ManagerModel';
import { WorkspaceDataModel } from '@/apis/models/WorkspaceModel';
import QuizEventApi from '@/apis/QuizEventApi';
import { QuizEventPreview } from '@/apis/models/QuizEventModel';

export default Vue.extend({
  components: {
    QuizEventCard,
  },
  data() {
    return {
    };
  },
  computed: {
    currentWorkspace(): WorkspaceDataModel {
      return this.$store.state.currentWorkspace;
    },
    quizEvents(): QuizEventPreview[] {
      return this.$store.state.theQuizEvents;
    },
  },
  async mounted() {
    // [Development] temperarily put manager info here
    await ManagerApi.getManagerEntryInfo().then((info: ManagerEntryInfoModel) => {
      this.$store.commit('updateWorkspaceList', info.workspaceList);
      this.$store.commit('changeWorkspace', info.workspaceList[0]);
      this.$store.commit('setUsername', info.username);
    });
    await QuizEventApi.getQuizEvents(this.currentWorkspace.uuid).then(
      (datalist) => {
        this.$store.commit('getQuizEvents', datalist);
      },
    );
  },
  methods: {
    async deleteEventCard(index: number) {
      if (index >= 0 && index < this.quizEvents.length) {
        await QuizEventApi.delete(this.currentWorkspace.uuid, this.quizEvents[index].uuid);
        this.quizEvents.splice(index, 1);
      }
    },
  },
});
